import React from 'react'

export default function FancyCard(props) {
    return (
        <div className="cont">
            <div className="left">
                <a href={props.data.image} target="_blank">
                    <div className="overlay">
                        <img src="assets/icons8-zoom-in-64.png" alt="search icon" />
                    </div>
                </a>
                <img className="image" alt={props.data.title} src={props.data.image} />
            </div>
            <div className="right">
                <h2 className="title">{props.data.title}</h2>
                <p>{props.data.description}</p>
                {props.data.link ? <a href={props.data.link} target="_blank"> open in a new tab </a> : ''}
            </div>
            <style jsx>{`
                .cont{
                    width: 100%;
                    height: auto;
                    display: grid;
                    grid-template-columns: 40% 40%;
                    grid-gap: 15px;
                    margin: 0;
                    margin-top: 50px;
                    padding: 0;
                
                    border: 1px solid #ffffff66;
                    border-radius: 11px;
                    overflow: hidden;
                }
                .right {
                    padding: 25px;
                }
                h2{
                    margin-top: 0;
                }
                .left{
                    max-height: 350px;
                    overflow: hidden;
                    position: relative;
                }
                .left .overlay{
                    position: absolute;
                    z-index: 1;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    background: #0000006b;
                    display: flex;
                    cursor: pointer;
                    opacity: 0;
                    backdrop-filter: blur(7px);
                    border-radius: 7px;
                    transition-duration: 350ms;
                }
                .left:hover .overlay{
                    opacity: 1;
                }
                .left .overlay img{
                    margin: auto;
                }
                .left .image {
                    width: 100%;
                    object-fit: cover;
                    object-position: top;
                    margin: 0;
                    padding: 0;
                    border-radius: 7px;
                    transition-duration: 500ms;
                    transition-delay: 100ms;
                }
                .left:hover .image{
                    transform: scale(1.2) translate(0, 10px);             
                }
                .title{
                    color: #fff;
                }
                a{
                    color: #ffff00;
                    text-decoration: none;
                }
                @media (max-width: 840px){
                    .cont {
                        grid-template-columns: 100%;
                        grid-template-rows: auto auto;
                        grid-gap: 0;

                    }
                    .left{
                        height: 50vw;
                    }
                }
            `}</style>
        </div>
    )
}
